  nav{
    width: 100%;
  }
}
@media only screen and (max-width:1024px) {
  nav li a{
    font-size: 13px;
  }
}
 
@media only screen and (max-width:768px) {
  .nav-right{
    width: 50%;
  }
  .nav-links{
    display: none;
  }
  .toggle-btn{
    display: block;
  }
  .nav-links-active{
    background-color: rgb(50, 50, 50);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 15;
    width: 30%;
    height: 100%;
    padding: 0 15px;
    margin-top: 0;
    display: block;
    -webkit-animation: nav-links-animation 500ms;
            animation: nav-links-animation 500ms;
  }
  @-webkit-keyframes nav-links-animation{
    from{
      -webkit-transform: translateX(-100%);
              transform: translateX(-100%);
    }
    to{
      -webkit-transform: translate(0);
              transform: translate(0);
    }
  }
  @keyframes nav-links-animation{
    from{
      -webkit-transform: translateX(-100%);
              transform: translateX(-100%);
    }
    to{
      -webkit-transform: translate(0);
              transform: translate(0);
    }
  }
  
  .nav-links-active li{
    display: block;
    padding: 20px 0;
  }
  .nav-links-active a{
    color: white;
  }
  .medical-service:hover .medical-service-category{
    display: none;
  }
}

